(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .directive('backNavbar', backNavbar);

  /** @ngInject */
  function backNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/backNavbar/backNavbar.html',
      scope: {
          content: '@',
          icon: '@',
          link: '@',
          removeMargin: '@'
      },
      controller: BackNavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function BackNavbarController($scope, $state, $window) {
      var vm = this;

      vm.back = function() {
        if(vm.link) {
          $state.go(vm.link);
        } else {
          $window.history.back();
        }
      };

      vm.content = $scope.content;
      vm.icon = $scope.icon;
      vm.link = $scope.link;
      vm.removeMargin = angular.isDefined($scope.removeMargin) && $scope.removeMargin;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

