(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .controller('Claim7Controller', Claim7Controller);

  /** @ngInject */
  function Claim7Controller($state, $stateParams, ClaimService, Upload) {
    var vm = this;

    vm.information = {};
    vm.ClaimBody = ClaimService.ClaimBody

    vm.text1 = 'Upload supporting documents';
    vm.text2 = 'Documents';
    vm.placeholder1 = 'Description (e.g. records, test results, etc.)';
    vm.text3 = 'Click here to add documents.';
    vm.text4 = '+ Add more document/s';

    vm.btn1 = 'Continue';

    vm.uploadFiles = function (files) {
      if (files && files.length) {
        for (var i = 0; i < files.length; i++) {
          var file = files[i];
          Upload.upload({
            url: 'https://general.sonic.dev.coherent.com.hk/general/v2/s3/fileupload',
            data: {
              myFile: files[i]
            }
          }).then(function (resp) {
            var url = resp.data;
            vm.information.files.push(_.merge(_.pick(file, 'name', 'description'), {
              url: url
            }));
          }, function () {
            // error handling
          }, function () {
            // progress handler
          });
        }
      }
    }

    vm.proceed = function() {
      ClaimService.updateInformation(vm.information, $stateParams.id);
      vm.ClaimBody.Header.ClaimItems[0].Attachments[0].Url = ClaimService.claims[0].files[0].url
      vm.ClaimBody.Header.ClaimItems[0].Attachments[0].Descr = ClaimService.claims[0].files[0].name
      ClaimService.claims[0].files[0].name
      ClaimService.setClaimBody(vm.ClaimBody)

      $state.go('claim-8');
    };

    function init() {
      if($stateParams.id) {
        if(ClaimService.claims[parseInt($stateParams.id, 10)]) {
          vm.information = ClaimService.claims[parseInt($stateParams.id, 10)];
        } else {
          $state.go('claim-1');
        }
      } else if(!_.isEmpty(ClaimService.claims)) {
        vm.information = ClaimService.claims[ClaimService.claims.length - 1];

        vm.information.files = vm.information.files ? vm.information.files : [];
      } else {
        $state.go('claim-1');
      }
    }

    init();
  }
})();

